$(function () {
  $("#lang").selectmenu();
});

['left', 'center', 'right'].forEach((el) => {
  const slider = $(`.slider__item--${el}`);
  const sliderWrapper = slider.children('.slider__item-wrapper');
  sliderWrapper.clone().appendTo(sliderWrapper);
})

const menu = $('#menu');

$('#menu-open').on('click', function () {
  $(this).toggleClass('active')
  menu.toggleClass('active')
})
